import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhEcommerce, VhQuerySales } from 'ionic-vhframeworks';

@Component({
  selector: 'app-edit-customer-tax',
  templateUrl: './edit-customer-tax.component.html',
  styleUrls: ['./edit-customer-tax.component.scss']
})
export class EditCustomerTaxComponent {

  /** Dữ liệu invoice được chọn */
  @Input() invoice: any;
  /** Dữ liệu invoice được chọn */
  @Input() ecommerce_platform: any;
  /** Dữ liệu invoice được chọn */
  @Input() infoShop: any;
  /** Khi đóng modal */
  @Output() onClose = new EventEmitter<any>();
  /** Khi submit modal */
  @Output() onOk = new EventEmitter<any>();

  /** FormGroup của modal */
  customerTaxForm: FormGroup;
  /** Có phải khách hàng thành viên hay không */
  isCustomerMember: boolean = false;



  constructor(
    private vhQuerySales: VhQuerySales,
    private vhEcommerce: VhEcommerce,
  ) { }



  ngOnInit() {
    this.initForm(this.invoice);
  }



  /**
   * Khởi tạo form
   */
  initForm(invoice) {
    this.customerTaxForm = new FormGroup({
      tax_buyerDisplayName: new FormControl(invoice.tax_buyerDisplayName ? invoice.tax_buyerDisplayName : 'Người mua không lấy hóa đơn'),
      tax_buyerLegalName: new FormControl(invoice.tax_buyerLegalName ? invoice.tax_buyerLegalName : ''),
      tax_buyerTaxCode: new FormControl(invoice.tax_buyerTaxCode ? invoice.tax_buyerTaxCode : ''),
      tax_buyerAddressLine: new FormControl(invoice.tax_buyerAddressLine ? invoice.tax_buyerAddressLine : ''),
      tax_buyerEmail: new FormControl(invoice.tax_buyerEmail ? invoice.tax_buyerEmail : '', [Validators.email]),
    });
    if (invoice.id_customer !== 'id_retail') {
      this.isCustomerMember = true;
      this.customerTaxForm.get('tax_buyerDisplayName').addValidators(Validators.required);
      this.customerTaxForm.get('tax_buyerDisplayName').updateValueAndValidity();
      this.customerTaxForm.get('tax_buyerLegalName').addValidators(Validators.required);
      this.customerTaxForm.get('tax_buyerLegalName').updateValueAndValidity();
      this.customerTaxForm.get('tax_buyerTaxCode').addValidators(Validators.required);
      this.customerTaxForm.get('tax_buyerTaxCode').updateValueAndValidity();
      this.customerTaxForm.get('tax_buyerAddressLine').addValidators(Validators.required);
      this.customerTaxForm.get('tax_buyerAddressLine').updateValueAndValidity();
    }
  }


  /**
   * Khi nhấn nút Save
   */
  onSubmit() {
    // Nếu chưa valid thì đánh dấu dirty tất cả các control để hiện lỗi cho người dùng
    if (!this.customerTaxForm.valid) {
      for (const control in this.customerTaxForm.controls) {
        if (this.customerTaxForm.controls.hasOwnProperty(control)) {
          this.customerTaxForm.controls[control].markAsDirty();
          this.customerTaxForm.controls[control].updateValueAndValidity();
        }
      }
      return;
    }
    else {
      switch (this.ecommerce_platform) {
        case 'shopee':
          this.vhEcommerce.updateSalesOrderShopee(this.infoShop._id, this.invoice['order_sn'].toString(), { ...this.customerTaxForm.value });
          break;
        case 'lazada':
          this.vhQuerySales.updateSalesOrderLazada(this.infoShop._id, this.invoice['order_id'].toString(), { ...this.customerTaxForm.value });
          break;
        case 'tiktok':
          this.vhEcommerce.updateSalesOrderTiktok(this.infoShop._id, this.invoice['id'].toString(), { ...this.customerTaxForm.value });
          break;
        case 'tiki':
          this.vhQuerySales.updateSalesOrderTiki(this.infoShop._id, this.invoice['code'].toString(), { ...this.customerTaxForm.value });
          break;

        default:
          break;
      }

      this.onOk.emit(this.customerTaxForm.value);
    }
  }

}
